html,
body {
  height: 100%;
  overflow: hidden;
}
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App {
  display: flex;
  flex: 1 0 auto;
  align-items: stretch;
}

.DialogContainer {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
